#header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    padding: 0;
    z-index: 999;
    @include media('sp') {
        height: 6.0rem;
    }
    &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
        height: 1px;
        background: #D0D0D0;
        z-index: 0;
    }
    .header-wrapper {
        position: relative;
        display: block;
        padding: 1.5rem 0;
        margin: 0;
        z-index: 2;
        @include media('sp') {
            padding: 1.8rem 6.5rem 1.8rem 0;
        }
    }
    .container {
        position: relative;
        z-index: 2;
    }
    .header-logo {
        position: relative;
        float: left;
        width: fit-content;
        padding: 0;
        a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            text-decoration: none;
            outline: none;
            width: 20.0rem;
            max-width: 100%;
            margin: 0;
            @include media('sp') {
                width: 16.0rem;
            }
            img {
                width: 100%;
            }
        }
    }
    .header-collapse {
        position: relative;
        float: right;
        display: flex;
        align-items: center;
        justify-content: center;
        width: fit-content;
        &.show {
            display: flex !important;
        }
        .navbar-nav {
            position: relative;
            flex: 0 0 auto;
            width: auto;
            &:not(:first-child) {
                margin-left: 2.0rem;
            }
        }
        .block-menu {
            display: flex;
            margin-left: -1.0rem;
            margin-right: -1.0rem;
            &>li {
                position: relative;
                padding: 0 1.0rem;
            }
        }
        .nav-link {
            padding: 0.3rem 0;
            @include media('sp') {
                padding: 0;
            }
            &:hover,
            &:focus {
                color: inherit;
            }
        }
        .dropdown-menu {
            padding: 0;
            font-size: 1.6rem;
            line-height: 2.4rem;
            color: #274767;
            @include media('sp') {
                font-size: 1.4rem;
                line-height: 2.4rem;
            }
        }
        .dropdown-divider {
            margin: 0;
        }
        .dropdown-item {
            padding: 1.2rem 1.5rem;
            color: #274767;
            @include media('sp') {
                padding: 0.8rem 1.2rem;
            }
        }
        @include media('sp') {
            .hidden-md {
                display: none;
            }
        }
    }
    .header-user {
        position: relative;
        float: right;
        padding: 0.3rem 0;
        margin-left: 2.0rem;
        @include media('sp') {
            display: none;
        }
        .user-name {
            position: relative;
            display: inline-block;
            font-weight: normal;
            font-size: 1.6rem;
            line-height: 2.4rem;
            text-align: center;
            white-space: nowrap;
            color: $base_color;
            i {
                display: inline-block;
                vertical-align: text-bottom;
                padding-bottom: 0.3rem;
                margin-right: 0.5rem;
            }
        }
    }
    .header-nav {
        position: relative;
        display: block;
        @include media('sp') {
            display: none;
        }
        .nav-menu {
            position: relative;
            list-style: none;
            display: flex;
            align-items: center;
            justify-content: center;
            line-height: 1;
            padding: 0;
            margin: 0;
            &>li {
                position: relative;
                padding: 0;
                flex: 0 0 auto;
                width: 20%;
            }
            .menu-link {
                position: relative;
                display: inline-block;
                font-family: $font_noto;
                font-size: 1.6rem;
                font-weight: bold;
                line-height: 2.4rem;
                color: $base_color;
                text-align: center;
                width: 100%;
                padding: 1.8rem 1.5rem 1.8rem;
                margin: 0;
                white-space: nowrap;
                text-decoration: none;
                cursor: pointer;
                z-index: 0;
                transition: all 0.2s ease-in-out;
                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    display: block;
                    height: 0;
                    background: $teal_color;
                    transition: all 0.2s ease-in-out;
                    z-index: 1;
                }
                &:hover,
                &.active {
                    &::before {
                        height: 0.3rem;
                    }
                }
            }
        }
    }
}