.mobile-nav-toggle {
  position: fixed;
  right: 0;
  top: 0;
  width: 6.0rem;
  height: 6.0rem;
  padding: 1.8rem 1.5rem;
  background: #569EE3;  
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 1001;
  margin: 0;
  transition: all 0.4s ease-in-out;
  @include media('sp') {
    display: inline-block;
  }
  .toggle-icon {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    span {
      display: inline-block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #FFFFFF;
      transition: all 0.4s ease-in-out;
      transform-origin: center center;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: calc(50% - 1px);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
  }
  &.toggle-active {
    .toggle-icon {
      span {
        &:nth-child(1) {
          transform: translateY(1.0rem) rotate(-40deg);
        }
        &:nth-child(2) {
          opacity: 0;
          visibility: hidden;
        }
        &:nth-child(3) {
          transform: translateY(-1.1rem) rotate(40deg);
        }
      }
    }
  }
}

// Mobile Nav
#mobile-nav {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 998;
  right: -100%;
  width: 80%;
  max-width: 500px;
  overflow-y: auto;
  transition: all 0.4s ease-in-out;
  background: #F2F2F2;
  .mobile-nav-container {
    position: relative;
    padding: 10.0rem 3.0rem 4.0rem;
    @include media('sp') {
      padding: 9.0rem 1.5rem 3.0rem;
    }
  }
  .mobile-user {
    position: relative;
    text-align: left;
    margin-bottom: 2.4rem;
    .user-name {
      position: relative;
      display: inline-block;
      font-weight: bold;
      font-size: 1.6rem;
      line-height: 2.4rem;
      text-align: left;
      white-space: nowrap;
      color: $base_color;
      i {
        display: inline-block;
        vertical-align: text-bottom;
        padding-bottom: 0.3rem;
        margin-right: 0.5rem;
      }
    }
  }
  .mobile-nav-menu {
    position: relative;
    list-style: none;
    border-top: 1px solid #D9D9D9;
    & > li {
      position: relative;
      display: block;
      & > a {
        position: relative;
        display: block;
        width: 100%;
        font-family: $font_noto;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 2.4rem;
        text-align: left;
        color: #274767;
        padding: 1.8rem 0;
        text-decoration: none;
        outline: none;
        border-bottom: 1px solid #D9D9D9;
        &::after {
          content: "\e905";
          position: absolute;
          top: 50%;
          right: 0;
          font-family: 'icomoon' !important;
          speak: never;
          font-size: 80%;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          line-height: 1;
          -webkit-font-smoothing: antialiased;
          transform: translateY(-50%);
        }
      }
    }
    .drop-down {
      & > a {
        &::after {
          content: "";
          position: absolute;
          top: calc(50% - 3px);
          right: 0;
          display: inline-block;
          width: 10px;
          height: 6px;
          background-image: url(../img/common/icon-down.png);
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          z-index: 2;
        }
      }
      ul {
        display: none;
        overflow: hidden;
      }
      li {
        padding-left: 16px;
        a {
          position: relative;
          display: block;
          width: 100%;
          font-weight: normal;
          font-size: 14px;
          line-height: 24px;
          text-align: left;
          color: #000000;
          padding: 16px 0;
          text-decoration: none;
          outline: none;
          border-bottom: 1px solid #000000;
        }
      }
      &.active {
        & > a::after {
          transform: rotate(180deg);
          transform-origin: center center;
        }
      }
    }
  }
}

.mobile-nav-overly {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 997;
  background: rgba($color: #000000, $alpha: 0.5);
  display: none;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}


/* Mobile Nav body classes */
body.mobile-nav-active {
  @include media('sp') {
    overflow: hidden;
  }
}
body.mobile-nav-active #mobile-nav {
  right: 0;
}