// section
.common-section {
    position: relative;
    padding: 6.0rem 0 7.0rem;
    @include media('sp') {
        padding: 5.0rem 0 6.0rem;
    }
}

// text set
.mx-txt {
    position: relative;
    font-weight: normal;
    font-size: 1.8rem;
    @include media('sp') {
        font-size: 1.6rem;
    }
}

.m-title {
    position: relative;
    font-weight: bold;
    font-size: 2.0rem;
}

.text-list {
    position: relative;
    list-style-type: "ãƒ»";
    padding-left: 1.8rem;
}

.underline_bg {
    display: inline;
    background: linear-gradient(transparent 68%, #FBB03B 0%);
}


/* btn */

.btn-wrap {
    position: relative;
}

.viewmore_link_wrap {
    position: relative;
    .viewmore_link {
        position: relative;
        display: inline-block;
        font-family: $font_ubuntu;
        font-weight: bold;
        font-style: italic;
        font-size: 16px;
        line-height: 26px;
        color: $orange_color;
        text-decoration: none;
        &::before {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: $orange_color;
        }
        &::after {
            content: ">";
            display: inline-block;
            line-height: 1;
            margin-left: 6px;
            vertical-align: text-bottom;
        }
        &:hover {
            &::before {
                width: 100%;
                animation: link-lineloop 0.6s ease-out;
                -webkit-animation: link-lineloop 0.6s ease-out;
            }
        }
    }
}

.view-more {
    position: relative;
}

.btn {
    position: relative;
}

.btn-block {
    display: block;
    width: 100%;
}

.btn {
    position: relative;
    display: inline-block;
    padding: 1.2rem 4.0rem;
    font-family: $font_noto;
    font-size: 1.6rem;
    font-weight: normal;
    line-height: 2.4rem;
    color: #FFFFFF;
    white-space: nowrap;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    background-color: #569EE3;
    border: 1px solid #569EE3;
    border-radius: 0.6rem;
    transition: all 0.3s ease-in-out;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
        padding: 1.0rem 2.4rem;
    }
    i {
        position: relative;
        display: inline-block;
        vertical-align: text-bottom;
        padding-bottom: 0.3rem;
        margin-right: 1.0rem;
        z-index: 3;
        @include media('sp') {
            margin-right: 0.6rem;
        }
    }
    &:focus,
    &:hover {
        background-color: #558BBE;
        border-color: #558BBE;
    }
}

.btn-group-sm>.btn,
.btn-sm {
    padding: 1.0rem 3.0rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
    border-radius: 0.6rem;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
        padding: 0.8rem 2.4rem;
    }
    i {
        padding-bottom: 0.3rem;
        margin-right: 0.6rem;
    }
}

.link-btn {
    position: relative;
    overflow: hidden;
    display: inline-block;
    width: fit-content;
    max-width: 100%;
    padding: 1.2rem 4.0rem;
    background-color: $teal_color;
    border: 1px solid $teal_color;
    border-radius: 0.6rem;
    font-family: $font_noto;
    font-size: 1.6rem;
    line-height: 2.4rem;
    font-weight: normal;
    color: #FFFFFF;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    outline: none;
    outline: none !important;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    z-index: 0;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
        padding: 1.0rem 2.4rem;
    }
    span {
        position: relative;
        color: inherit;
        text-decoration: none;
        z-index: 3;
    }
    i {
        position: relative;
        display: inline-block;
        vertical-align: text-bottom;
        padding-bottom: 0.3rem;
        margin-right: 1.0rem;
        z-index: 3;
        @include media('sp') {
            margin-right: 0.6rem;
        }
    }
    &:focus,
    &:hover {
        color: #FFFFFF;
        text-decoration: none;
        background-color: #558BBE;
        border-color: #558BBE;
        outline: none !important;
        span, i {
            text-decoration: none;
            color: inherit;
        }
    }
}

.btn-success {
    color: #FFFFFF;
    background-color: #169E49;
    border-color: #169E49;
    &:focus,
    &:hover {
        background-color: #228E4A;
        border-color: #228E4A;
    }
}

.btn-danger {
    color: #FFFFFF;
    background-color: #E60012;
    border-color: #E60012;
    &:focus,
    &:hover {
        background-color: #B1000E;
        border-color: #B1000E;
    }
}

.btn-info {
    color: #FFFFFF;
    background-color: #569EE3;
    border-color: #569EE3;
    &:focus,
    &:hover {
        background-color: #558BBE;
        border-color: #558BBE;
    }
}

.btn-outline {
    background-color: #FFFFFF;
    color: #569EE3;
    &:focus,
    &:hover {
        background-color: #569EE3;
        color: #FFFFFF;
    }
    &.btn-success {
        background-color: #FFFFFF;
        color: #169E49;
        &:focus,
        &:hover {
            background-color: #169E49;
            color: #FFFFFF;
        }
    }
    &.btn-danger {
        background-color: #FFFFFF;
        color: #E60012;
        &:focus,
        &:hover {
            background-color: #E60012;
            color: #FFFFFF;
        }
    }
    &.btn-info {
        background-color: #FFFFFF;
        color: #569EE3;
        &:focus,
        &:hover {
            background-color: #569EE3;
            color: #FFFFFF;
        }
    }
}

.btn:disabled {
    cursor: not-allowed;
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    display: none;
    outline: 0;
    opacity: 0;
    transition: opacity .1s linear;
    padding: 1.5rem;
    overflow-x: hidden;
    overflow-y: auto;
    background: rgba($color: #000000, $alpha: 0.5);
    .modal-dialog {
        position: relative;
        max-width: 70.0rem;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        pointer-events: none;
        transition: transform .3s ease-out, -webkit-transform .3s ease-out;
        transform: translate(0, -15%);
        margin: 7.5rem auto;
        @include media('sp') {
            margin: 1.5rem 0;
        }
    }
    .modal-header {
        position: relative;
        padding: 2.0rem 2.4rem;
        border-bottom: 1px solid #D9D9D9;
        @include media('sp') {
            padding: 1.6rem 1.6rem;
        }
        .modal-title {
            position: relative;
            font-size: 2.4rem;
            line-height: 3.6rem;
            font-weight: bold;
            color: $base_color;
            text-align: left;
            @include media('sp') {
                font-size: 1.8rem;
                line-height: 2.7rem;
            }
        }
    }
    .modal-content {
        position: relative;
        display: block;
        width: 100%;
        background: #FFFFFF;
        border-radius: 0.6rem;
        border: 1px solid rgba($color: #000000, $alpha: 0.2);
        pointer-events: auto;
    }
    .close {
        position: absolute;
        top: 1.6rem;
        right: 1.0rem;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1.5rem 1.2rem;
        font-size: 1.6rem;
        line-height: 2.4rem;
        font-weight: normal;
        color: #569EE3;
        outline: none !important;
        border: none !important;
        background-color: transparent;
        margin: 0;
        z-index: 3;
        @include media('sp') {
            top: 1.2rem;
            right: 1.0rem;
            padding: 1.0rem 0.6rem;
            font-size: 1.4rem;
            line-height: 2.1rem;
        }
    }
    .modal-body {
        position: relative;
        padding: 2.4rem 1.2rem 2.4rem 2.4rem;
        @include media('sp') {
            padding: 2.4rem 0.6rem 2.4rem 1.6rem;
        }
    }
    .modal-form {
        position: relative;
        display: block;
        padding-right: 0.8rem;
    }
    .modal-scroll {
        position: relative;
        padding-right: 2.4rem;
        max-height: 50.0rem;
        -webkit-overflow-scrolling: touch;
        overflow-y: auto;
        @include media('sp') {
            max-height: 40.0rem;
            padding-right: 1.2rem;
        }
        &::-webkit-scrollbar-track {
            background-color: #F2F2F2;
            border-radius: 0.8rem;
        }
        &::-webkit-scrollbar {
            width: 0.8rem;
            border-radius: 0.8rem;
            background-color: #F2F2F2;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0.8rem;
            background-color: #D9D9D9;
        }
        @include media('sp') {
            &::-webkit-scrollbar-track {
                border-radius: 0.6rem;
            }
            &::-webkit-scrollbar {
                width: 0.6rem;
                border-radius: 0.6rem;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 0.6rem;
            }
        }
    }
    .modal-footer {
        position: relative;
        padding: 1.0rem 1.2rem;
        border-top: 1px solid #D9D9D9;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        .btn {
            margin: 0.6rem 1.2rem;
            @include media('sp') {
                margin: 0.5rem 1.0rem;
            }
        }
    }
    &.show {
        opacity: 1;
        .modal-dialog {
            transform: translate(0, 0);
        }
    }
}

.common-modal {
    .modal-header {
        background-color: #FFFFFF !important;
        .close {
            margin: 0;
        }
    }
    .modal-body {
        position: relative;
        padding: 2.4rem 2.4rem;
        @include media('sp') {
            padding: 2.4rem 1.6rem;
        }
    }
    .modal-footer {
        .btn {
            padding: 1.2rem 3.0rem;
            min-width: 20.0rem;
            @include media('sp') {
                min-width: 18.0rem;
                padding: 0.8rem 2rem;
            }
            i {
                display: inline;
                vertical-align: baseline;
                padding-bottom: 0;
                margin-right: 0;
            }
        }
    }
}

.modal-open {
    overflow: hidden;
}

.dropdown-item:focus,
.dropdown-item:hover {
    color: #274767;
    text-decoration: none;
    background-color: #f8f9fa !important;
}

.dropdown-item.active, 
.dropdown-item:active {
    background-color: #FFFFFF;
    color: #274767;
}

