    border-radius: 1.2rem;
}

#card_title {
    font-weight: bold;
}

.card-header {
    padding: 1.2rem 1.6rem;
}

.list-group-item {
    padding: 1.2rem 1.6rem;
}

.common-form {
    position: relative;
    .table-responsive,
    .table-container {
        .table {
            position: relative;
            width: 100%;
            border-collapse: collapse;
            font-family: "Noto Sans JP", sans-serif;
            font-size: 1.4rem;
            line-height: 2.4rem;
            font-weight: normal;
            border: none;
            margin-bottom: 0;
            th,
            td {
                padding: 1.8rem 1.2rem;
                border-bottom: 1px solid #D9D9D9;
            }
            thead {
                th,
                td {
                    padding: 1.8rem 1.2rem;
                    background-color: #FAFAFA;
                    font-weight: bold;
                    border-bottom: 1px solid #D9D9D9;
                }
            }
            caption {
                padding-top: 0.6rem;
                padding-bottom: 0.6rem;
                text-align: right;
            }
            .action-td {
                padding-top: 1.2rem;
                padding-bottom: 1.2rem;
            }
        }
        .table.table-sm {
            td,
            th {
                padding: 1.8rem 0.6rem;
                &:first-child {
                    padding-left: 1.2rem;
                }
            }
            thead {
                th,
                td {
                    padding: 1.8rem 1.2rem;
                    background-color: #FAFAFA;
                    font-weight: bold;
                    border-bottom: 1px solid #D9D9D9;
                }
            }
            .action-td {
                padding-top: 1.2rem;
                padding-bottom: 1.2rem;
            }
        }
        .btn {
            color: #FFFFFF;
        }
    }
    .card {
        .table-responsive,
        .table-container {
            .table.table-sm {
                td,
                th {
                    padding: 1.2rem 0.6rem;
                    &:first-child {
                        padding-left: 1.2rem;
                    }
                    &:last-child {
                        padding-right: 1.2rem;
                    }
                }
                thead {
                    th,
                    td {
                        padding: 1.8rem 1.2rem;
                        background-color: #FAFAFA;
                        font-weight: bold;
                        border-bottom: 1px solid #D9D9D9;
                    }
                }
                .action-td {
                    padding-top: 1.2rem;
                    padding-bottom: 1.2rem;
                }
            }
        }
    }
    div.dataTables_wrapper {
        &>.row {
            &:not(:last-child) {
                margin-bottom: 3.0rem;
                @include media('sp') {
                    margin-bottom: 2.0rem;
                }
            }
            @include media('sp') {
                .col-md-6:not(:last-child) {
                    margin-bottom: 2.0rem;
                }
            }
        }
    }
    @include media('sp') {
        div.dataTables_wrapper div.dataTables_length,
        div.dataTables_wrapper div.dataTables_filter,
        div.dataTables_wrapper div.dataTables_info,
        div.dataTables_wrapper div.dataTables_paginate {
            text-align: right;
        }
    }
    table.dataTable.table-sm .sorting:before,
    table.dataTable.table-sm .sorting_asc:before,
    table.dataTable.table-sm .sorting_desc:before {
        top: 1.8rem;
    }
    table.dataTable.table-sm .sorting:after,
    table.dataTable.table-sm .sorting_asc:after,
    table.dataTable.table-sm .sorting_desc:after {
        top: 1.8rem;
    }
    .btn {
        padding: 1.2rem 3.0rem;
        color: #FFFFFF;
        @include media('sp') {
            padding: 0.8rem 2rem;
        }
        i {
            padding: 0;
            margin: 0;
            vertical-align: baseline;
        }
    }
    .btn-sm {
        position: relative;
        display: inline-block;
        padding: 0.6rem 1.2rem;
        font-size: 1.4rem;
        border: none;
        i {
            padding: 0;
            margin: 0;
            vertical-align: baseline;
        }
    }
    .form-table {
        position: relative;
        width: 100%;
        display: block;
        font-size: 1.6rem;
        line-height: 2.4rem;
        color: #274767;
        font-weight: normal;
        text-align: left;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
        tbody {
            display: block;
            width: 100%;
        }
        tr {
            display: grid;
            grid-template-columns: 20.0rem minmax(0px, 1fr);
            grid-column-gap: 3.0rem;
            width: 100%;
            @include media('sp') {
                grid-template-columns: 100%;
                grid-column-gap: 2.0rem;
                grid-row-gap: 0.8rem;
            }
            &:not(:last-child) {
                margin-bottom: 3rem;
                @include media('sp') {
                    margin-bottom: 2.0rem;
                }
            }
        }
        th {
            position: relative;
            display: block;
            font-weight: 600;
            @include media('pc') {
                line-height: 4.8rem;
            }
        }
        td {
            position: relative;
            display: block;
        }
        .form-input {
            position: relative;
        }
        .validate {
            color: #dc3545;
            margin-top: 0.5rem;
        }
    }
    .btn-group {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-left: -1.5rem;
        margin-right: -1.5rem;
        margin-top: 4.0rem;
        @include media('sp') {
            margin-left: -1.0rem;
            margin-right: -1.0rem;
            margin-top: 2.4rem;
        }
        &>li {
            position: relative;
            padding: 0 1.5rem;
            @include media('sp') {
                flex: 0 0 100%;
                width: 100%;
                padding: 0 1.0rem;
                &:not(:last-child) {
                    margin-bottom: 2.0rem;
                }
            }
        }
        .form-btn {
            min-width: 24.0rem;
            margin: 0 auto;
            @include media('sp') {
                min-width: 18.0rem;
                max-width: 100%;
            }
        }
    }
    .pagination {
        justify-content: center;
        margin: 3rem auto 2rem;
    }
    .selectize-input {
        padding: 1.2rem 1.6rem;
        border: 1px solid #D9D9D9;
        border-radius: 0.6rem;
    }
    .selectize-control.multi .selectize-input.has-items {
        padding: 1.0rem 1.0rem 0.8rem 1.2rem;
    }
    .selectize-dropdown,
    .selectize-input,
    .selectize-input input {
        color: #303030;
        font-family: inherit;
        font-size: 1.6rem;
        line-height: 2.4rem;
        -webkit-font-smoothing: inherit;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.1rem;
        }
    }
}

