}

.common-search-form {
    position: relative;
    .search-form-group {
        position: relative;
        display: flex;
        width: fit-content;
        max-width: 100%;
        margin-left: auto;
        margin-right: 0;
        @include media('sp') {
            width: 100%;
        }
    }
    .search_input {
        position: relative;
        padding: 0;
        @include media('sp') {
            flex: 1 auto;
        }
        input {
            position: relative;
            z-index: 0;
        }
        .clear-search {
            position: absolute;
            top: calc(52% - 0.6rem);
            right: 1.2rem;
            display: none;
            width: 1.2rem;
            height: 1.2rem;
            cursor: pointer;
            &::before,
            &::after {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                display: inline-block;
                width: 100%;
                height: 2px;
                background: #569EE3;
            }
            &::before {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
            &::after {
                transform: translate(-50%, -50%) rotate(45deg);
            }
        }
    }
    input[type='text'] {
        position: relative;
        width: 30.0rem;
        max-width: 100%;
        height: 4.8rem;
        font-family: $font_noto;
        font-size: 1.6rem;
        font-weight: normal;
        line-height: 2.4rem;
        padding: 0.8rem 1.8rem;
        color: #274767;
        border-radius: 0.6rem 0 0 0.6rem;
        margin: 0;
        @include media('sp') {
            height: 4.2rem;
            width: 20rem;
            width: 100%;
            font-size: 1.4rem;
            line-height: 2.4rem;
            padding: 0.8rem 1.2rem;
            border-radius: 0.4rem 0 0 0.4rem;
        }
    }
    button {
        position: relative;
        flex: 0 0 auto;
        width: 10.0rem;
        height: 4.8rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #569EE3;
        padding: 0.8rem 1.2rem;
        font-family: $font_noto;
        font-size: 16px;
        line-height: 28px;
        font-weight: normal;
        letter-spacing: 0.1em;
        color: #FFFFFF;
        text-decoration: none;
        outline: none;
        cursor: pointer;
        border: 2px solid #569EE3;
        transition: all 0.4s;
        border-radius: 0 0.6rem 0.6rem 0;
        z-index: 1;
        @include media('sp') {
            height: 4.2rem;
            font-size: 1.4rem;
            line-height: 2.4rem;
            padding: 0.8rem 1.2rem;
            width: 9.0rem;
            border-radius: 0 0.4rem 0.4rem 0;
        }
        svg {
            position: relative;
            z-index: 3;
            margin-right: 0.6rem;
            @include media('sp') {
                width: 1.6rem;
            }
        }
        span {
            position: relative;
            color: inherit;
            text-decoration: none;
            z-index: 3;
        }
    }
}

#search_blocked_form {
    margin-bottom: 3.0rem;
}

.php-info .center {
    text-align: center;
}

.php-info .center table {
    margin: 3.0rem auto;
}

.php-info th,
.php-info td {
    padding: 1.2rem 1.6rem;
}

.php-info .e {
    width: 24.0rem;
}

// wp-pagination
.wp-pagination {
    position: relative;
    margin-top: 6.0rem;
    @include media('sp') {
        margin-top: 5.0rem;
    }
    .wp-pagenavi {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        span,
        a {
            margin-left: 0.4rem;
            margin-right: 0.4rem;
            margin-bottom: 0.8rem;
            padding: 0.7rem 1.5rem;
            min-width: 40px;
            border: 1px solid #00A99D;
            display: inline-block;
            font-size: 1.4rem;
            font-weight: normal;
            line-height: 2.4rem;
            text-align: center;
            color: #00A99D;
            transition: all 0.3s ease-in-out;
            @include media('sp') {
                margin-left: 0.3rem;
                margin-right: 0.3rem;
                margin-bottom: 0.6rem;
                padding: 0.6rem 1.2rem;
            }
            &:hover {
                background: #00A99D;
                color: #FFFFFF;
            }
        }
        span {
            background: #00A99D;
            color: #FFFFFF;
            &.pages {
                display: none;
            }
        }
    }
}