// pc&sp
.pc {
    display: block !important;
}

.sp {
    display: none !important;
}

.tc {
    display: block !important;
}

.tb {
    display: none !important;
}

@include media('tb') {
    .tc {
        display: none !important;
    }
    .tb {
        display: block !important;
    }
}

@include media('sp') {
    .pc {
        display: none !important;
    }
    .sp {
        display: block !important;
    }
}

.hide {
    display: none;
}

//hide
@include media('pc') {
    .pc-hide {
        display: none !important;
    }
}

@include media('sp') {
    .hidden-sm {
        display: none;
    }
    .sp-hide {
        display: none !important;
    }
}

// wrapper
#wrapper {
    position: relative;
    width: 100%;
}

main {
    position: relative;
    &#main {
        min-height: calc(100vh - 10.7rem);
        @include media('sp') {
            min-height: calc(100vh - 10.8rem);
        }
    }
}


/* input */

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea,
.form-control,
.selectize-input {
    position: relative;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 0.6rem;
    box-sizing: border-box;
    padding: 1.2rem 1.6rem;
    width: 100%;
    font-family: $font_noto;
    font-weight: normal;
    font-size: 1.6rem;
    line-height: 2.4rem;
    text-align: left;
    color: #B8BCC0;
    outline: none !important;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.1rem;
        padding: 1.2rem 1.2rem;
    }
}

.selectize-control.multi .selectize-input.has-items {
    padding: 1.0rem 1.0rem 0.8rem 1.2rem;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
.form-control,
.selectize-input {
    height: 5.0rem;
    @include media('sp') {
        height: 4.7rem;
    }
}

select.form-control[multiple],
select.form-control[size],
textarea.form-control {
    height: auto;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
    border-color: #569EE3;
    color: #274767;
}

select {
    position: relative;
    -webkit-appearance: none;
    -moz-appearance: none;
    background-image: url(../img/icon-down.svg);
    background-position: calc(100% - 1.2rem) 50%;
    background-size: 1.1rem 0.6rem;
    background-repeat: no-repeat;
    z-index: 0;
    @include media('sp') {
        background-position: calc(100% - 1.0rem) 50%;
    }
}


/* checkbox */

.form-checkbox {
    display: block;
    position: relative;
    padding-left: 2.8rem;
    font-family: $font_noto;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.4rem;
    text-align: left;
    color: #000000;
    user-select: none;
    cursor: pointer;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
        padding-left: 2.4rem;
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
    .checkmark {
        position: absolute;
        top: 0.3rem;
        left: 0;
        height: 2.0rem;
        width: 2.0rem;
        background-color: #E6E6E6;
        border-radius: 0.3rem;
        border: 1px solid #569EE3;
        transition: all 0.2s ease-in-out;
        @include media('sp') {
            width: 1.6rem;
            height: 1.6rem;
            top: 0.5rem;
        }
    }
    &:hover {
        input~.checkmark {
            background-color: #E6E6E6;
        }
    }
    .checkmark::after {
        content: "";
        position: absolute;
        display: none;
    }
    .checkmark::after {
        left: 0.5rem;
        top: 0.1rem;
        width: 0.8rem;
        height: 1.2rem;
        border: solid #FFFFFF;
        border-width: 0 0.2rem 0.2rem 0;
        transform: rotate( 45deg);
        @include media('sp') {
            left: 0.4rem;
            top: 0;
            width: 0.6rem;
            height: 1.0rem;
        }
    }
    input:checked~.checkmark {
        background-color: #569EE3;
    }
    input:checked~.checkmark::after {
        display: block;
    }
}

// radiobox
.form-radiobox {
    display: block;
    position: relative;
    padding-left: 2.8rem;
    font-family: $font_noto;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.4rem;
    text-align: left;
    color: #000000;
    user-select: none;
    cursor: pointer;
    @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
        padding-left: 2.4rem;
    }
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
    .checkmark {
        position: absolute;
        top: 0.3rem;
        left: 0;
        height: 2.0rem;
        width: 2.0rem;
        background-color: #E6E6E6;
        border-radius: 50%;
        border: 1px solid #569EE3;
        transition: all 0.2s ease-in-out;
        @include media('sp') {
            width: 1.6rem;
            height: 1.6rem;
            top: 0.5rem;
        }
    }
    .checkmark::after {
        content: "";
        position: absolute;
        display: none;
    }
    .checkmark:after {
        left: 0.4rem;
        top: 0.4rem;
        width: 1.0rem;
        height: 1.0rem;
        border-radius: 50%;
        background: #569EE3;
        @include media('sp') {
            width: 0.8rem;
            height: 0.8rem;
            left: 0.3rem;
            top: 0.3rem;
        }
    }
    input:checked~.checkmark {
        background-color: #E6E6E6;
    }
    input:checked~.checkmark::after {
        display: block;
    }
}

// picture
picture,
figure,
.image_wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
    width: 100%;
}

// scroll
.scroll {
    position: relative;
    width: 100%;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    @include media('tb') {
        padding-bottom: 1.6rem;
        &::-webkit-scrollbar-track {
            background-color: #F2F2F2;
            border-radius: 0.8rem;
        }
        &::-webkit-scrollbar {
            height: 0.8rem;
            border-radius: 0.8rem;
            background-color: #F2F2F2;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0.8rem;
            background-color: #D9D9D9;
        }
    }
    @include media('sp') {
        padding-bottom: 1.2rem;
        &::-webkit-scrollbar-track {
            border-radius: 0.6rem;
        }
        &::-webkit-scrollbar {
            height: 0.6rem;
            border-radius: 0.6rem;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0.6rem;
        }
    }
}