.mainvisual {
    position: relative;
    padding: 0;
    .mainvisual-bg {
        position: relative;
        width: 100%;
    }
}

.pageindex {
    position: relative;
    height: 20.0rem;
    padding: 3.6rem 0 4.0rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../img/pageindex.jpg);
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: cover;
    @include media('sp') {
        background-image: url(../img/pageindex-sp.jpg);
        height: 20.0rem;
        padding: 3.0rem 0 3.6rem;
    }
    .wrapper {
        position: relative;
        flex: 0 0 100%;
        width: 100%;
    }
    .pageindex-title {
        position: relative;
        .lead {
            position: relative;
            font-weight: 900;
            font-size: 3.0rem;
            letter-spacing: 0.2em;
            line-height: 4.5rem;
            text-align: center;
            color: #633E1C;
            @include media('sp') {
                font-size: 2.4rem;
                line-height: 3.6rem;
                letter-spacing: 0.1em;
            }
        }
        .sub {
            position: relative;
            font-weight: 900;
            font-size: 1.8rem;
            line-height: 3.0rem;
            text-align: center;
            color: #D0C5BB;
            margin-top: 0.4rem;
            @include media('sp') {
                font-size: 1.6rem;
                line-height: 2.8rem;
                margin-top: 0.2rem;
            }
        }
    }
}

.breadcrumbs {
    position: relative;
    padding: 1.2rem 0;
    ol {
        display: block;
        list-style: none;
        margin: 0;
        font-family: $font_noto;
        font-size: 1.6rem;
        line-height: 2.8rem;
        font-weight: normal;
        color: #999999;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
        a {
            color: inherit;
            text-decoration: underline;
        }
        li {
            position: relative;
            display: inline;
            &+li {
                &::before {
                    content: "\f105";
                    display: inline-block;
                    font-family: "Font Awesome 6 Free";
                    font-size: inherit;
                    font-weight: 900;
                    font-style: normal;
                    padding-left: 1.2rem;
                    padding-right: 1.2rem;
                    @include media('sp') {
                        padding-left: 1.0rem;
                        padding-right: 1.0rem;
                    }
                }
            }
        }
    }
}

.inner-toppage {
    position: relative;
    padding: 5.0rem 0 6.0rem;
    @include media('sp') {
        padding: 4.0rem 0 5.0rem;
    }
}

.inner-page {
    position: relative;
    padding: 6.0rem 0 7.0rem;
    @include media('sp') {
        padding: 5.0rem 0 6.0rem;
    }
}

.dashboard-wrapper {
    position: relative;
    max-width: 90.0rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.dashboard-card {
    position: relative;
    display: block;
    padding: 4.0rem;
    background: #FFFFFF;
    border-radius: 0.8rem;
    border: 1px solid rgba($color: #000000, $alpha: 0.125);
    box-shadow: 0 0.6rem 3.0rem rgba($color: #000000, $alpha: 0.08);
    @include media('sp') {
        padding: 2.4rem 1.8rem;
        border-radius: 0.6rem;
    }
    .card-header {
        position: relative;
        display: block;
        padding: 0;
        background: none;
        border: none;
        border-radius: 0;
        margin-bottom: 3.0rem;
        @include media('sp') {
            margin-bottom: 2.0rem;
        }
        &::after {
            content: "";
            visibility: hidden;
            height: 0;
            display: block;
            font-size: 0;
            clear: both;
        }
        .lead {
            position: relative;
            font-size: 2.4rem;
            font-weight: bold;
            line-height: 3.6rem;
            @include media('sp') {
                font-size: 1.8rem;
                line-height: 2.7rem;
            }
            i {
                display: inline-block;
                color: $teal_color;
                margin-right: 1.0rem;
                vertical-align: text-bottom;
                padding-bottom: 0.4rem;
                @include media('sp') {
                    padding-bottom: 0.3rem;
                    margin-right: 0.8rem;
                }
            }
        }
    }
    .card-title {
        position: relative;
        font-size: 2.4rem;
        line-height: 3.6rem;
        font-weight: bold;
        margin-bottom: 3.0rem;
    }
    .card-body {
        position: relative;
        padding: 0;
        flex: none;
    }
    .caption {
        position: relative;
        font-size: 2.0rem;
        line-height: 3.0rem;
        font-weight: bold;
        margin-bottom: 2.0rem;
        @include media('sp') {
            font-size: 1.6rem;
            line-height: 2.4rem;
            margin-bottom: 1.6rem;
        }
        small {
            display: inline-block;
            font-size: 1.6rem;
            font-weight: normal;
            margin-left: 0.4rem;
            @include media('sp') {
                font-size: 1.4rem;
                line-height: 2.4rem;
            }
        }
    }
    .description {
        position: relative;
        font-weight: normal;
        font-size: 1.8rem;
        line-height: 1.5;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
    }
}

.layout-card {
    position: relative;
    display: block;
    padding: 5.0rem 6.0rem 6.0rem;
    background: #FFFFFF;
    border-radius: 0.8rem;
    border: 1px solid rgba($color: #000000, $alpha: 0.125);
    box-shadow: 0 0.6rem 3.0rem rgba($color: #000000, $alpha: 0.08);
    @include media('sp') {
        padding: 2.4rem 1.8rem;
        border-radius: 0.6rem;
    }
    .card-header {
        position: relative;
        display: block;
        padding: 0;
        background: none;
        border: none;
        border-radius: 0;
        margin-bottom: 4.0rem;
        @include media('sp') {
            margin-bottom: 2.0rem;
        }
        .lead {
            position: relative;
            font-size: 2.4rem;
            font-weight: bold;
            line-height: 3.6rem;
            @include media('sp') {
                font-size: 1.8rem;
                line-height: 2.7rem;
            }
            i {
                display: inline-block;
                color: $teal_color;
                margin-right: 1.0rem;
                vertical-align: text-bottom;
                padding-bottom: 0.4rem;
                @include media('sp') {
                    padding-bottom: 0.3rem;
                    margin-right: 0.8rem;
                }
            }
        }
    }
    .card-body {
        position: relative;
        padding: 0;
        flex: none;
    }
}

.form-lead {
    position: relative;
    font-size: 2.0rem;
    line-height: 3.0rem;
    font-weight: bold;
    color: $base_color;
    margin-bottom: 2.0rem;
    @include media('sp') {
        font-size: 1.8rem;
        line-height: 2.7rem;
        margin-bottom: 1.6rem;
    }
}

.form-card-wrapper {
    position: relative;
    max-width: 90.0rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.form-card {
    position: relative;
    display: block;
    padding: 4.0rem 4.0rem;
    background: #F4FAFF;
    border-radius: 0.8rem;
    @include media('sp') {
        padding: 2.4rem 1.8rem;
        border-radius: 0.6rem;
    }
    .card-title {
        position: relative;
        font-size: 2.0rem;
        line-height: 3.0rem;
        font-weight: bold;
        margin-bottom: 3.0rem;
        @include media('sp') {
            font-size: 1.8rem;
            line-height: 2.7rem;
            margin-bottom: 2.0rem;
        }
    }
    .card-body {
        position: relative;
        padding: 0;
        flex: none;
    }
    .description {
        position: relative;
        font-weight: normal;
        font-size: 1.8rem;
        line-height: 2.7rem;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
    }
    &.card-sm {
        padding: 2.4rem 2.4rem;
        @include media('sp') {
            padding: 2.4rem 1.8rem;
        }
        .card-title {
            margin-bottom: 2.0rem;
            @include media('sp') {
                margin-bottom: 1.6rem;
            }
        }
    }
}

.sign-form-card {
    position: relative;
    display: block;
    max-width: 50.0rem;
    width: 100%;
    padding: 5.0rem 4.0rem 6.0rem;
    background: #FFFFFF;
    border: 1px solid rgba($color: #000000, $alpha: 0.125);
    border-radius: 0.8rem;
    box-shadow: 0 0.6rem 3.0rem rgba($color: #000000, $alpha: 0.08);
    margin-left: auto;
    margin-right: auto;
    @include media('sp') {
        padding: 2.4rem 1.8rem 3.0rem;
        border-radius: 0.6rem;
    }
    .card-title {
        position: relative;
        font-size: 2.4rem;
        line-height: 3.6rem;
        font-weight: bold;
        text-align: center;
        margin-bottom: 4.0rem;
        @include media('sp') {
            font-size: 1.8rem;
            line-height: 2.7rem;
            margin-bottom: 2.0rem;
        }
    }
    .card-body {
        position: relative;
        padding: 0;
        flex: none;
    }
    .sign-form {
        position: relative;
        max-width: 36.0rem;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    .description {
        position: relative;
        font-weight: normal;
        font-size: 1.8rem;
        line-height: 2.7rem;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
    }
}

.describe-panel {
    position: relative;
    padding: 3.6rem 3.2rem;
    background: #F4FAFF;
    border-radius: 0.8rem;
    @include media('sp') {
        padding: 2.4rem 2.0rem;
        border-radius: 0.6rem;
    }
    .inner-row {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        .inner-left {
            position: relative;
            flex: 0 0 auto;
            @include media('tb') {
                width: 100%;
            }
        }
        .inner-right {
            position: relative;
            flex: 0 0 auto;
            @include media('tb') {
                width: 100%;
                margin-top: 3.6rem;
            }
            @include media('sp') {
                margin-top: 2.4rem;
            }
        }
    }
    .describe-list {
        position: relative;
        list-style: none;
        display: flex;
        margin-left: -1.6rem;
        @include media('tb') {
            justify-content: center;
            margin-left: -1.6rem;
            margin-right: -1.6rem;
        }
        @include media('sp') {
            flex-wrap: wrap;
            max-width: 24.0rem;
            margin-left: auto;
            margin-right: auto;
        }
        &>li {
            position: relative;
            flex: 0 0 auto;
            width: auto;
            padding: 0 5.0rem;
            @include media('tb') {
                padding: 0 5.0rem;
            }
            @include media('sp') {
                width: 100%;
                padding: 0;
            }
            &:not(:last-child) {
                border-right: 1px solid #D9D9D9;
                @include media('sp') {
                    border-right: none;
                    padding-bottom: 2.0rem;
                    border-bottom: 1px solid #D9D9D9;
                    margin-bottom: 2.0rem;
                }
            }
        }
        .describe-item {
            position: relative;
            @include media('sp') {
                padding: 0 2.0rem;
                margin-left: auto;
                margin-right: auto;
            }
            .label {
                position: relative;
                font-weight: bold;
                font-size: 1.8rem;
                line-height: 2.7rem;
                color: #526C85;
                margin-bottom: 0.8rem;
                @include media('sp') {
                    font-size: 1.6rem;
                    line-height: 2.4rem;
                    text-align: center;
                    margin-bottom: 0.4rem;
                }
            }
            .value {
                position: relative;
                font-family: 'DIN 2014';
                font-weight: bold;
                font-size: 3.0rem;
                line-height: 3.6rem;
                color: #274767;
                @include media('sp') {
                    font-size: 2.8rem;
                    line-height: 3.3rem;
                    text-align: center;
                }
                strong {
                    color: inherit;
                }
                small {
                    display: inline-block;
                    font-size: 1.6rem;
                    line-height: 2.7rem;
                    color: #526C85;
                    margin-left: 0.6rem;
                    vertical-align: bottom;
                    @include media('sp') {
                        font-size: 1.4rem;
                        line-height: 2.6rem;
                        margin-left: 0.4rem;
                    }
                }
            }
        }
    }
    .download-action {
        position: relative;
        text-align: center;
    }
}

.table-responsive {
    display: block;
    width: 100%;
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    @include media('tb') {
        padding-bottom: 1.6rem;
        &::-webkit-scrollbar-track {
            background-color: #F2F2F2;
            border-radius: 0.8rem;
        }
        &::-webkit-scrollbar {
            height: 0.8rem;
            border-radius: 0.8rem;
            background-color: #F2F2F2;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0.8rem;
            background-color: #D9D9D9;
        }
    }
    @include media('sp') {
        padding-bottom: 1.2rem;
        &::-webkit-scrollbar-track {
            border-radius: 0.6rem;
        }
        &::-webkit-scrollbar {
            height: 0.6rem;
            border-radius: 0.6rem;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 0.6rem;
        }
    }
    .table {
        position: relative;
        width: 100%;
        min-width: 70.0rem;
        font-family: $font_noto;
        font-size: 1.6rem;
        font-weight: normal;
        line-height: 2.4rem;
        color: $base_color;
        text-align: left;
        word-break: break-all;
        border-collapse: collapse;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
            min-width: 56.0rem;
        }
        tr:nth-of-type(odd) {
            background: transparent;
        }
        th,
        td {
            position: relative;
            padding: 1.0rem 2.0rem;
            vertical-align: middle;
            border-top: none;
            border-bottom: 1px solid #D9D9D9;
            @include media('sp') {
                padding: 1.0rem 1.0rem;
                &:first-child {
                    padding-left: 1.6rem;
                }
            }
        }
        thead {
            background-color: #FAFAFA;
            th {
                padding-top: 2.3rem;
                padding-bottom: 2.3rem;
                font-weight: bold;
                @include media('sp') {
                    padding-top: 1.8rem;
                    padding-bottom: 1.8rem;
                }
            }
        }
        a {
            color: $teal_color;
        }
        caption {
            padding-top: 1.8rem;
            padding-bottom: 1.8rem;
            color: #526C85;
            text-align: left;
            caption-side: bottom;
            @include media('sp') {
                padding-top: 1.6rem;
                padding-bottom: 1.6rem;
            }
        }
        .date {
            width: 16.4rem;
            @include media('sp') {
                width: 12.8rem;
            }
        }
        .action {
            width: 12.4rem;
            text-align: center;
            @include media('sp') {
                width: 10.0rem;
            }
        }
        .no-result {
            padding-top: 1.8rem;
            padding-bottom: 1.8rem;
            @include media('sp') {
                padding-top: 1.6rem;
                padding-bottom: 1.6rem;
            }
        }
        &.table-sm {
            thead {
                th {
                    padding-top: 2.0rem;
                    padding-bottom: 2.0rem;
                    font-weight: bold;
                    @include media('sp') {
                        padding-top: 1.8rem;
                        padding-bottom: 1.8rem;
                    }
                }
            }
            .no-result {
                padding-top: 1.8rem;
                padding-bottom: 1.8rem;
                @include media('sp') {
                    padding-top: 1.6rem;
                    padding-bottom: 1.6rem;
                }
            }
        }
    }
}

.pagination {
    position: relative;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin-left: -0.6rem;
    margin-right: -0.6rem;
    margin-top: 3.0rem;
    @include media('sp') {
        margin-left: -0.4rem;
        margin-right: -0.4rem;
        margin-top: 2.4rem;
    }
    .page-link {
        position: relative;
        display: block;
        padding: 0.8rem 1.6rem;
        margin: 0;
        font-size: 1.4rem;
        font-weight: normal;
        line-height: 2.4rem;
        color: #569EE3;
        text-align: center;
        text-decoration: none;
        outline: none;
        background-color: #FFFFFF;
        border: 1px solid #D9D9D9;
        border-radius: 0.6rem;
        transition: all 0.2s ease-in-out;
        @include media('sp') {
            padding: 0.6rem 1.4rem;
        }
        &:hover {
            background-color: #569EE3;
            border-color: #569EE3;
            color: #FFFFFF;
        }
    }
    .page-item {
        position: relative;
        padding: 0 0.6rem;
        @include media('sp') {
            padding: 0 0.4rem;
        }
        &.active {
            .page-link {
                z-index: 3;
                color: #FFFFFF;
                background-color: #569EE3;
                border-color: #569EE3;
            }
        }
        &.disabled {
            .page-link {
                color: #999999;
                pointer-events: none;
                cursor: auto;
                background-color: #FFFFFF;
                border-color: #D9D9D9;
            }
        }
        &:first-child,
        &:last-child {
            .page-link {
                border-radius: 0.6rem;
            }
        }
    }
    .disabled,
    .disabled .switch span.active {
        color: #D9D9D9;
        pointer-events: none;
        cursor: not-allowed;
    }
}

.table-action {
    position: relative;
    margin-top: 3.0rem;
    @include media('sp') {
        margin-top: 2.0rem;
    }
    .viewmore {
        position: relative;
        display: inline-block;
        font-size: 1.6rem;
        font-weight: normal;
        color: $teal_color;
        @include media('sp') {
            font-size: 1.4rem;
        }
        &:hover {
            text-decoration: underline;
        }
        i {
            display: inline-block;
            font-size: 80%;
            margin-left: 1.0rem;
            @include media('sp') {
                margin-left: 0.6rem;
            }
        }
    }
}

.dashboard-card {
    .dropzone {
        position: relative;
        display: block;
        background: #FAFAFA;
        width: 100%;
        height: 25.0rem;
        padding: 4.3rem 2.4rem 4.7rem;
